.my-home {
  height: 570px;
}
.my-home .article-list {
  width: 70%;
  padding: 6px;
  height: 100%;
  overflow: scroll;
}
.my-home .article-list .visit-list {
  font-size: 12px;
  box-shadow: 0 0 4px #ddd;
  border-radius: 10px;
  padding: 10px;
  min-height: 38px;
  overflow: hidden;
}
.my-home .article-list .visit-list span {
  color: #aaa;
}
.my-home .article-list .visit-list .int {
  display: inline-block;
  width: 10px;
}
.my-home .article-list .saohua {
  -webkit-transition: 0.5s;
  transition: 0.5s;
  font-size: 12px;
  color: #eee;
  font-family: 'cursive';
  margin: 10px 0;
  text-align: center;
}
.my-home .article-list .saohua:hover {
  -webkit-transition: 0.5s;
  transition: 0.5s;
  font-size: 18px;
  color: #F45C24;
}
.my-home .article-list .list {
  -webkit-transition: 0.5s;
  transition: 0.5s;
  box-shadow: 0 0 4px #ddd;
  border-radius: 10px;
  overflow: hidden;
  padding: 10px;
  margin-bottom: 16px;
}
.my-home .article-list .list .list-item {
  float: left;
  width: 50%;
  padding: 5px;
}
.my-home .article-list .list .list-item a {
  color: #999;
  display: inline-block;
  width: 375px;
  vertical-align: middle;
}
.my-home .article-list .list .list-item a:hover {
  color: #333;
}
.my-home .article-list .list:hover {
  -webkit-transition: 0.5s;
  transition: 0.5s;
  box-shadow: 0 0 4px #F45C24;
}
.my-home .side-bar {
  position: fixed;
  right: 38px;
  top: 110px;
  z-index: 2;
  width: 23%;
  height: 600px;
}
.my-home .side-bar .search {
  -webkit-transition: 0.5s;
  transition: 0.5s;
  box-shadow: 0 0 4px #ddd;
  border-radius: 10px;
  overflow: hidden;
  height: 38px;
  line-height: 38px;
  margin-bottom: 38px;
}
.my-home .side-bar .search .ant-input,
.my-home .side-bar .search .ant-input:focus,
.my-home .side-bar .search .ant-input:hover {
  border: none;
  box-shadow: none;
}
.my-home .side-bar .personal {
  -webkit-transition: 0.5s;
  transition: 0.5s;
  box-shadow: 0 0 4px #ddd;
  border-radius: 10px;
  padding: 10px;
  margin-top: 16px;
}
.my-home .side-bar .personal .ant-descriptions-title {
  margin-bottom: 5px;
}
.my-home .side-bar .personal .avatar-user {
  display: flex;
  align-items: center;
}
.my-home .side-bar .personal .avatar-user .username {
  margin-left: 10px;
}
.my-home .side-bar .statistics {
  -webkit-transition: 0.5s;
  transition: 0.5s;
  box-shadow: 0 0 4px #ddd;
  border-radius: 10px;
  padding: 10px;
  margin-top: 16px;
  text-align: center;
}
